.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.right {
  display: flex;
  flex-direction: column;
}

textarea {
  width: 100%;
  height: 100%;
  margin-top: 50px;
}

form {
  display: flex;
  flex-direction: column;
  padding-right: 50px;
  margin-right: 50px;
  border-right: 1px solid rgb(176, 176, 176);
  width: 500px;
}

h1 {
  margin-top: 100px;
  margin-bottom: 100px;
}

h2,
p {
  text-align: left;
}

label {
  font-weight: 500;
}

input {
  height: 30px;
  margin-bottom: 20px;
}

label {
  text-align: left;
}
